import React, { useState } from 'react';
import { Button, Form, Typography } from 'antd';
import Template from '../Template';
import { vegetablesDataBar } from '../../common/data/bar/vegetablesDataBar';
import { duzinaDataBar } from '../../common/data/bar/duzinaDataBar';
import TotalButton from '../TotalButton';
import Order from '../Order';
import withOrderProps from '../withOrderProps';
import { BackButton, useShowPopup, WebAppProvider } from '@vkruglikov/react-telegram-web-app';

const Bar = ({ totalVisible, onHide }) => {
  const [buttonState, setButtonState] = useState();
  const showPopup = useShowPopup();
  return (
    <WebAppProvider
  options={{
    smoothButtonsTransition: true,
  }}
>
    <div className="flex flex-col relative overflow-y-hidden">
      <>
      <Typography.Title level={3}>BackButton</Typography.Title>
      <Form
        labelCol={{ span: 6 }}
        name="BackButtonDemo"
        layout="horizontal"
        autoComplete="off"
      >
        <Form.Item>
          <Button
            block
            type="primary"
            onClick={() =>
              setButtonState({
                show: !buttonState?.show,
              })
            }
          >
            {buttonState?.show ? 'Hide BackButton' : 'Show BackButton'}
          </Button>
        </Form.Item>
      </Form>
      <div>
        {buttonState?.show && (
          <BackButton
            onClick={() => {
              showPopup({
                message: 'back button click',
              });
            }}
          />
        )}
      </div>
    </>
      <Template vegetablesData={vegetablesDataBar} duzinaData={duzinaDataBar} />
      <TotalButton />
      <Order totalVisible={totalVisible} isVisible={onHide} />
    </div>
    </WebAppProvider>
  );
};

export default withOrderProps(Bar);